@font-face {
  font-family: Century;
  src: url("CenturyGothic.873f4e9a.ttf");
}

@font-face {
  font-family: Avant;
  src: url("AvantGardeRegular.3c2d5e8e.ttf");
}

canvas {
  position: fixed;
  top: 0;
  left: 0;
}

body {
  background-color: #ffce00;
  margin: 0;
  overflow: hidden;
}

main {
  grid-template-columns: repeat(12, 1fr);
  display: grid;
  position: relative;
}

header {
  color: #000;
  text-align: left;
  z-index: 100;
  grid-column: 2 / span 2;
  font-family: Avant;
  font-size: 35px;
  font-weight: 600;
  position: relative;
  top: 80px;
}

#overlayText {
  color: #000;
  z-index: 10;
  grid-column: 2 / span 2;
  font-family: Avant;
  font-size: 18px;
  position: relative;
  top: 100px;
}

#resetView {
  color: #fff;
  cursor: pointer;
  z-index: 10;
  background-color: #ce1111;
  border: none;
  border-radius: 5px;
  grid-column: 2 / span 1;
  padding: 10px 15px;
  font-family: Avant;
  font-size: 16px;
  font-weight: 400;
  position: relative;
  top: 180px;
}

#resetView:hover {
  color: #fff;
  background-color: #ce11114f;
}

#playOpenLid {
  color: #fff;
  cursor: pointer;
  z-index: 10;
  background-color: #000;
  border: none;
  border-radius: 5px;
  grid-column: 2 / span 1;
  padding: 10px 15px;
  font-family: Avant;
  font-size: 16px;
  font-weight: 400;
  position: relative;
  top: 80px;
}

#playOpenLid:hover {
  color: #fff;
  background-color: #00000069;
}
